$main-menu-bg: url("./img/bg/Main-Menu.png");
$select-char-bg: url("./img/bg/Select-Char.png");
$game-bg: url("./img/bg/BG-Gaol.jpg");
@font-face {
  font-family: "Prompt-Reg";
  src: url("./Fonts/Prompt-Regular_0.ttf");
}
@font-face {
  font-family: "Prompt-Bold";
  src: url("./Fonts/Prompt-Bold_0.ttf");
}

body {
  margin: 0;
  #gameCanvas {
    background-image: $select-char-bg;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
    position: relative;

    .start-menu {
      height: 100vh;
      width: 100%;
      position: absolute;
      background-image: $main-menu-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      .visa-logo {
        padding-top: 10vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 65%;
        img {
          width: 100%;
        }
      }

      .game-logo {
        margin-top: 2vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 90%;
        img {
          width: 100%;
        }
      }

      .play-btn {
        display: flex;
        justify-content: center;
        width: 100%;
        padding-top: 5vh;
        margin-left: auto;
        margin-right: auto;

        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2em;
          padding: 1vh 3rem 1vh 3rem;
        }
      }

      .reward-btn {
        width: fit-content;
        padding-top: 2vh;
        margin-left: auto;
        margin-right: auto;

        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2em;
          padding: 1vh 1rem 1vh 1rem;
        }
      }

      .football {
        margin-left: auto;
        margin-right: auto;
        margin-top: 5vh;
        max-width: 85%;
        img {
          width: 100%;
        }
      }
    }

    .loading-menu {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      background-image: $main-menu-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      width: 100%;
      height: 100vh;
      margin: 0 auto;
      .fifa-logo {
        position: relative;
        max-width: 90%;
        img {
          width: 100%;
        }
      }
    }

    .select-char-menu {
      width: 100%;
      background-image: $select-char-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      object-fit: cover;

      .player-head {
        padding-top: 2vh;
        text-align: center;
        width: 100%;
        position: absolute;
        font-family: "Prompt-Bold";
        color: white;
      }

      .button-char {
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        top: 63%;
        width: 100%;
        gap: 10px;

        .btn-row {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 10px;

          button {
            font-family: "Prompt-Bold";
            clip-path: polygon(15% 0, 100% 0%, 85% 100%, 0% 100%);
            border-radius: 0 0.8rem 0 0.8rem;
            padding: 1vh 1rem 1vh 1rem;
          }

          button#load_argen {
            background-color: #6d1f49;
            color: #ffffff;
          }

          button#load_belgium {
            background-color: #c01633;
            color: #ffffff;
          }

          button#load_brazil {
            background-color: #7aa9d7;
            color: #ffffff;
          }

          button#load_england {
            background-color: #001789;
            color: #ffffff;
          }

          button#load_france {
            background-color: #f48f1c;
            color: #ffffff;
          }

          button#load_portugal {
            background-color: #991a25;
            color: #ffffff;
          }

          button#load_qatar {
            background-color: #2f9847;
            color: #ffffff;
          }

          button#load_spain {
            background-color: #016933;
            color: #ffffff;
          }
        }
      }

      .main-game-btn {
        display: flex;
        justify-content: center;
        width: 100%;
        position: absolute;
        top: 84%;

        button {
          font-family: "Prompt-Bold";
          font-size: 1.2rem;
          padding: 1vh 2rem 1vh 2rem;
          background-color: red;
          color: white;
          border-radius: 25px;
        }
      }
    }

    .game-interface {
      position: absolute;
      top: 93vh;
      .ui-container{
        margin: 0 0 0 1rem;
        display: flex;
        img {
          width: 10%;
          height: 10%;
        }

        font-family: "Prompt-Bold";
        color: white;
        text-shadow: 2px 4px #5a0713;
  
        .total-ball {
          margin: 0.5vh 0 0 0.6rem;
          font-size: 1.5rem;
        }

        .score-word{
          margin: 0.5vh 0 0 12.5rem;
          font-size: 1.5rem;
        }
        .football-score{
          margin: 0.5vh 0 0 0.5rem;
          font-size: 1.5rem;
        }
      }

    }

    .final-score-ui {
      // background-image: $game-bg;
      // background-repeat: no-repeat;
      // background-size: 100% 100%;
      // background-position: center;

      width: 100%;
      position: absolute;
      flex-direction: column;

      .token-left{
        padding-top: 3vh;
        display: flex;
        margin: 0 0 0 1rem;
        width: 8%;
        img {
          width: 100%;
        }

        p{
          font-family: "Prompt-Bold";
          margin: 0 0 0 ;
          position: absolute;
          top: 2.8vh;
          left: 3.5rem;
          font-size: 1.5rem;
          text-shadow: 2px 4px #5a0713;
          color: #ffffff;
        }
      }

      .score-word {
        height: 10vh;
        margin: 10vh auto 0 auto;
        display: flex;
        justify-content: center;
        width: 70%;
        img{
          width: 100%;
        }
      }

      .final-score{
        padding-top: 3vh;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        width: 80%;
        img {
          width: 100%;
        }

        p{
          font-family: "Prompt-Bold";
          font-style: italic;
          margin: 0;
          top: 28vh;
          position: absolute;
          font-size: 5rem;
          color: #ffffff;
        }
      }

      .play-again-btn {
        display: flex;
        justify-content: center;
        padding-top: 8vh;
        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2rem;
          padding: 1vh 1.2rem 1vh 1.2rem;
        }
      }

      .check-reward{
        display: flex;
        justify-content: center;
        padding-top: 3vh;
        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2rem;
          padding: 1vh 2rem 1vh 2rem;
        }
      }

    }

  }
}
